import React, { Component } from 'react';
import Home from './components/Home';
import './styles/common.css';

class App extends Component {
    render() {
    	const { socket } = this.props
        return (
            <div className=''>
                <Home socket={socket} />
            </div>
        );
    }
}

export default App;




