import React, { Component } from 'react';
import { Button, FormGroup, FormControl, ControlLabel } from "react-bootstrap";

import './style.css';

const _ = require('underscore');

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
             userName: '',
             password: ''
        }
    }

    componentWillMount() {
       
    }

    validateForm() {
        return this.state.userName.length > 0 && this.state.password.length > 0;
      }

      handleChange = event => {
        this.setState({
          [event.target.id]: event.target.value
        });
      }

   
    render() {
        return (
            <div className='login-wrapper'>
                <div className='login-form-wrapper'>
                <form>
                  <FormGroup controlId="userName" bsSize="large">
                    <ControlLabel className='label WickedGrit'>Username</ControlLabel>
                    <FormControl
                      autoFocus
                      type="text"
                      value={this.state.userName}
                      onChange={this.handleChange}
                    />
                  </FormGroup>
                  <FormGroup controlId="password" bsSize="large">
                    <ControlLabel className='label WickedGrit'>Password</ControlLabel>
                    <FormControl
                      value={this.state.password}
                      onChange={this.handleChange}
                      type="password"
                    />
                  </FormGroup>
                  <Button
                    block
                    bsSize="large"
                    disabled={!this.validateForm()}
                    type="button"
                    onClick={this.props.handleLogin.bind(this,this.state.userName, this.state.password)}
                  >
                    Login
                  </Button>
                </form>
                </div>
            </div>
        );
    }
}

export default Login;




