import React, { Component } from 'react';
import '../../styles/common.css';
import './style.css';

const _ = require('underscore');

class Categories extends Component {
	constructor(props) {
        super(props);
        this.state = {
            visible: true,
            categories: [],
            current_category: "BEER",
        }
    }

    componentDidMount() {
        const categories = _.uniq(_.pluck(this.props.categories, 'mainCategory'));
        this.setState({ categories: categories, current_category: this.props.currentCategory });
    };

    componentWillReceiveProps(props){
        const categories = _.uniq(_.pluck(props.categories, 'mainCategory'));
        this.setState({ categories: categories, current_category: props.currentCategory });
    }

    renderCategoryIcon(category) {
    	let icon = require(`../../images/categories_icons/${category.replace(/ /g, '_').toLowerCase()}_transparent.png`);
    	if(this.state.current_category === category){
    		icon = require(`../../images/categories_icons/${category.replace(/ /g, '_').toLowerCase()}_full.png`);
    	}
    	return icon;
    }

    renderCategoryName(category) {
    	if(this.state.current_category === category){
    		return (
    			<div className='categories-item-name active WickedGrit'>
					{category}
				</div>
    		);
    	}
    	return (
			<div className='categories-item-name WickedGrit'>
				{category}
			</div>
		);
    }

    selectCategory(category) {
    	this.setState({ current_category: this.props.currentCategory });
    }

    renderCategoryList() {
    	return this.state.categories.map((category, idx) => 
			<div key={`category_${idx}`} className='categories-item-wrapper'>
				<div className='categories-item-icon'>
					<img alt={category} src={this.renderCategoryIcon(category)} />
				</div>
				{this.renderCategoryName(category)}
			</div>
		);
    }

    render() {
        return (
            <div className='categories-wrapper'>
                <div className='categories-list'>
                	{this.renderCategoryList()}
                </div>
                <div className='app-logo' onClick={this.props.logoutUser.bind(this)}>
					<img alt='app logo' src={require('../../images/logo.png')} />
				</div>
            </div>
        );
    }
}

export default Categories;




