import React, { Component } from 'react';
import { socketConnect } from 'socket.io-react';
import Categories from '../Categories';
import PriceList from '../PriceList';
import Login from '../Login';
import './style.css';

const _ = require('underscore');
const axios = require('axios');

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            categories: [],
            current_category: 'BEER',
            outletId: null
        }
    }

    componentWillMount() {
        var id = window.localStorage.getItem('outletId');
        if(id){
            this.setState({ outletId: id });
            this.initSocket(id);
        }
    }

    initSocket = (id) => {
        const { socket } = this.props
        socket.emit('outletId', id);
        socket.on('categories', msg => { this.setState({ categories: msg.arr }) });
        socket.on('drinks', msg => { this.makeCategories(msg)});
        socket.on('pricechanged', msg => {
            if(msg.outletId && msg.outletId.toString() === this.state.outletId.toString()){
                this.updateCategories(msg);
            }
        });
    }

    makeCategories(data) {
        if (!_.isEmpty(this.state.categories)) {
            this.setState({ data: data });
            const categories = this.state.categories;
            _.each(categories, (cat) => {
                const thisData = _.filter(data.arr, (d) => {
                    return cat.name === d.category;
                });
                cat.data = thisData;
                cat.card_type = 1;
            });        
            this.setState({ categories: categories });
            return categories;
        }
    }

    updateCategories(data) {
        const categories = this.makeCategories(this.state.data);
        if(categories){
            _.each(data.arr, (d) => {
                const thisData = _.find(categories, (cat)=>{
                    return cat.name === d.category;
                });
                const thisDrink = _.find(thisData.data, (td) => { return td.drinkId === d.drinkId });
                thisDrink.runningPrice = d.runningPrice;
            });
            this.setState({ categories: categories });
        }
    }

    onSlideChange(e, availableCat) {
        const categories = this.makeCategories(this.state.data);
        const thisCat = availableCat[e];
        if(thisCat){
            this.setState({ current_category: thisCat.mainCategory });
        }
    }

    handleLogin = (userName, password) => {
        axios.put('http://35.154.86.71:7777/tvlogin', {
            userName: userName,
            password: password
        }).then((response) => {
            if(response.data.success){
                this.setState({
                    outletId: response.data.data.outletId.toString()
                });
                this.setCurrentUserLocally(response.data.data);
            }
          })
          .catch((error) => {
            console.log(error);
          });
    }

    logoutUser = () => {
        window.localStorage.removeItem('outletId');
        this.setState({ outletId: null });
    }

    setCurrentUserLocally = (data) => {
        window.localStorage.setItem('outletId', data.outletId.toString());
        this.initSocket(data.outletId);
    }

    render() {
        if(_.isEmpty(this.state.outletId)){
            return (
                <div className='content-area'>
                    <Login handleLogin={this.handleLogin}/>
                </div>
            );
        }else{
            return (
                <div className=''>
                    <Categories categories={this.state.categories} currentCategory={this.state.current_category} logoutUser={this.logoutUser} />
                    <div className='content-area'>
                    	<PriceList categories={this.state.categories} onSlideChange={this.onSlideChange.bind(this)} />
                    </div>
                </div>
            );
        }
    }
}

export default socketConnect(Home);




